<template>
  <base-material-card
    color="success"
    :icon="icon"
    inline
    :title="title"
    class="px-5 py-3"
  >
    <v-row
      align="center"
      justify="end"
    >
      <v-col
        xs-cols="12"
        md-cols="2"
        class="text-end"
      >
        Total de items: {{ total }}
        <v-divider vertical />
      </v-col>
      <!-- Select de items por página -->
      <v-col
        class="mx-4"
        xs-cols="12"
        md-cols="3"
      >
        <v-select
          v-model="itemsPerPage"
          :items="[5,10,15,20,25,50]"
          label=""
          flat
          prefix="Mostrando "
          suffix="items por página"
        />
      </v-col>
      <v-divider vertical />
      <!-- Boton de agregar -->
      <v-col
        v-if="actions.includes('insert')"
        cols="2"
      >
        <v-btn
          color="primary"
          class="ml-1"
          small
        >
          <v-icon color="white">
            mdi-plus
          </v-icon>
          Agregar
        </v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-data-table
          :headers="columns"
          :items="items"
          :page.sync="page"
          :items-per-page.sync="itemsPerPage"
          hide-default-footer
          :single-select="singleSelect"
          :show-select="select"
          fixed-header
          calculate-widths
          @page-count="pageCount = $event"
        >
          <!-- Items -->
          <template v-slot:body="{ item }">
            <tbody>
              <tr
                v-for="item in items"
                :key="item"
              >
                <td
                  v-for="column in columns"
                  :key="column"
                >
                  <v-row
                    justify="center"
                    no-gutters
                  >
                    <!-- Campos de tipo imagen -->
                    <v-col
                      v-if="column.type === 'image'"
                      no-gutters
                    >
                      <img
                        :src="item[column.value]"
                        :alt="column.text"
                      >
                    </v-col>
                    <!-- Campos de tipo texto -->
                    <v-col
                      v-if="column.type === 'text'"
                      no-gutters
                    >
                      <p v-text="item[column.value]" />
                    </v-col>
                    <!-- Campos de tipo switch, para booleanos editables -->
                    <v-col
                      v-if="column.type === 'switch'"
                      class="text-center"
                      no-gutters
                    >
                      <v-switch
                        v-model="item[column.value]"
                        @change="checkedItemClick(item.id, column.value, item[column.value])"
                      />
                    </v-col>
                    <!-- Campos de tipo check, para booleanos de solo lectura -->
                    <v-col
                      v-if="column.type === 'bool'"
                      no-gutters
                    >
                      <v-checkbox
                        v-model="item[column.value]"
                        readonly
                        on-icon="mdi-check-circle"
                        off-icon=""
                      />
                    </v-col>
                    <!-- Botones de accion -->
                    <v-col
                      v-if="column.value === 'action'"
                      no-gutters
                    >
                      <!-- Accion de ver -->
                      <v-btn
                        v-if="actions.includes('view')"
                        class="px-2 ml-1"
                        :color="'info'"
                        min-width="0"
                        small
                        @click="viewItem(item.id)"
                      >
                        <v-icon
                          small
                          v-text="'mdi-eye'"
                        />
                      </v-btn>
                      <!-- Accion de editar -->
                      <v-btn
                        v-if="actions.includes('edit')"
                        class="px-2 ml-1"
                        :color="'success'"
                        min-width="0"
                        small
                        @click="editItem(item.id)"
                      >
                        <v-icon
                          small
                          v-text="'mdi-pencil'"
                        />
                      </v-btn>
                      <!-- Accion de eliminar -->
                      <v-btn
                        v-if="actions.includes('delete')"
                        class="px-2 ml-1"
                        :color="'error'"
                        min-width="0"
                        small
                        @click="deleteItem(item.id)"
                      >
                        <v-icon
                          small
                          v-text="'mdi-close'"
                        />
                      </v-btn>
                    </v-col>
                  </v-row>
                </td>
              </tr>
            </tbody>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
    <v-pagination
      v-model="page"
      :length="pageCount"
    />
  </base-material-card>
</template>

<script>
  export default {
    name: 'Datatable',
    props: {
      title: {
        default: 'Lista',
        type: String,
      },
      actions: {
        default: () => [],
        type: Array,
      },
      icon: {
        default: 'mdi-clipboard-text',
        type: String,
      },
      columns: {
        default: () => [],
        type: Array,
      },
      itemsPerPage: {
        default: 10,
        type: Number,
      },
      select: {
        default: false,
        type: Boolean,
      },
      singleSelect: {
        default: true,
        type: Boolean,
      },
      items: {
        default: () => [],
        type: Array,
      },
    },
    data () {
      return {
        page: 1,
        pageCount: 1,
        btnItems: false,
      }
    },
    computed: {
      total: function () {
        return this.items.length
      },
    },
    created () {
      if (this.actions.length > 1) {
        this.columns.push({ value: 'action', text: 'Acciones', sortable: false })
      }
    },
    methods: {
      // Emite el evento de vista del item, pasando como parámetro el id
      viewItem (itemId) {
        this.$emit('viewItem', itemId)
      },
      // Emite el evento de edicion, pasando como parámetro el id
      editItem (itemId) {
        this.$emit('edit', itemId)
      },
      // Emite el evento de edicion, pasando como parámetro el id
      checkedItemClick (itemId, field, value) {
        this.$emit('checkedItemClick', itemId, field, value)
      },
      // Modal para mostrar la confirmacion de eliminacion del item
      deleteItem (itemId) {

      },
    },
  }
</script>

<style scope>
  .v-select__selection {
    font-family: "Roboto", sans-serif;
    font-size: 16px
  }
  .theme--light.v-text-field > .v-input__control > .v-input__slot::before {
    border:none;
  }
  .theme--light.v-input:not(.v-input--is-disabled) input, .theme--light.v-input:not(.v-input--is-disabled) textarea {
    display: none;
  }
</style>
